import Header from '../../components/Header'
import { queryActivity, queryCoupons } from '../../api/API'
export default {
  name: 'activity',
  components: {
    Header
  },
  data () {
    return {
      couponsList: []
    }
  },
  methods: {
    async loadTopStrategy () {
      let res = await queryActivity(this.$store.state.User.token)
      if (res.code === 200) {
        this.couponsList = res.data
      }
    },
    async getCoupons (id) {
      let res = await queryCoupons(this.$store.state.User.token, id)
      if (res.code === 200) {
        return window.EventBus.$emit('globalMessage', res.msg)
      } else {
        return window.EventBus.$emit('globalMessage', res.msg)
      }
    }
  },
  mounted () {
    this.loadTopStrategy()
  }
}



